<template>
  <div id="app">
    <!-- 左侧侧边栏 -->
    <div v-if="!$route.meta.hideSidebar" class="sidebar">
      <div class="sidebar-header">
        <!-- 左侧边栏顶部：头像 + 姓名 + +号 -->
        <van-image
          round
          width="32px"
          height="32px"
          :src="require('./assets/images/my_profile.jpg')"
          alt="用户头像"
          @click="handleSidebarAvatarClick"
          class="avatar-hover"
        />
        <!-- 显示从localStorage获取的用户名 -->
        <span class="username">{{ username }}</span>
        
        <!-- +号弹出juli功能 -->
        <van-popover 
          v-model:show="showPopover" 
          :actions="actions" 
          placement="bottom-end" 
          :offset="[13, 8]"
          @select="handleActionSelect"
        >
          <template #reference>
            <van-icon name="plus" color="white" size="15" class="icon-hover"/>
          </template>
        </van-popover>
      </div>
      
      <div class="sidebar-menu">
        <!-- 替换原有的消息菜单项 -->
<div class="menu-group">
  <!-- 消息主菜单 -->
  <div 
    class="menu-item" 
    :class="{ 'active': active === 'message' }"
    @click="toggleMessageDropdown"  
  >
    <van-icon name="chat-o" class="menu-icon" />
    <span class="menu-text">面试</span>
    <!-- 下拉箭头（根据状态切换） -->
    <van-icon 
      :name="messageDropdownOpen ? 'arrow-up' : 'arrow-down'" 
      class="dropdown-arrow"
    />
  </div>

  <!-- 下拉菜单内容 -->
  <div class="dropdown-wrapper">
    <transition name="dropdown-fade">
      <div 
        class="message-dropdown" 
        v-if="messageDropdownOpen"
      >
        <div class="message-dropdown-content">
          <ul class="message-list">
            <li 
              v-for="(item, index) in friendMessageList" 
              :key="item.name" 
              class="message-item"
              @click="handleMessageItemClick(item)"
            >
              <van-image
                round
                width="40px"
                height="40px"
                :src="item.profile"
                class="message-avatar"
              />
              <span class="message-name">{{ item.name }}</span>
            </li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</div>
        <div 
          class="menu-item" 
          :class="{ 'active': active === 'friends' }"
          @click="navigateTo('/home/friends', 'friends')"
        >
          <van-icon name="friends-o" class="menu-icon" />
          <span class="menu-text">juli</span>
        </div>
        <div 
          class="menu-item" 
          :class="{ 'active': active === 'discover' }"
          @click="navigateTo('/home/discover', 'discover')"
        >
          <van-icon name="underway-o" class="menu-icon" />
          <span class="menu-text">动态</span>
        </div>
      </div>
    </div>

    <!-- 主内容区域 -->
    <div class="main-container">
      <!-- 内容区域 -->
      <div class="content-area">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      messageDropdownOpen: false,  // 控制下拉菜单显示/隐藏
    // 消息列表数据（面试官列表）
    friendMessageList: [
      { 
        name: "人工智能技术岗面试官", 
        profile: require('./assets/images/profile_拉菲.jpg') 
      },
      { 
        name: "大数据运维测试岗面试官", 
        profile: require('./assets/images/profile_02.jpg') 
      },
      { 
        name: "物联网产品岗面试官", 
        profile: require('./assets/images/profile_栗山未来.jpg') 
      }
    ],
      active: 'message',
      titleMap: {
        message: '面试',
        friends: 'juli',
        discover: '动态'
      },
      showPopover: false,
      actions: [
        { text: 'juli', icon: 'gift-o' },
      ],
      username: '' // 用于存储从localStorage获取的用户名
    }
  },
  computed: {
    currentTitle() {
      return this.titleMap[this.active] || '首页'
    }
  },
  methods: {
    // 切换消息下拉菜单
  toggleMessageDropdown() {
    this.messageDropdownOpen = !this.messageDropdownOpen;
    // 确保点击后消息菜单处于激活状态
    this.active = 'message';
  },
  
  // 点击消息列表项处理
handleMessageItemClick(item) {
  // 提取职业信息并存储
  const index = item.name.indexOf('面');
  const career = index !== -1 ? item.name.substring(0, index) : item.name;
  localStorage.setItem('career', career);
  
  // 大数据运维测试岗跳转到discover1路由
  if (item.name === "大数据运维测试岗面试官") {
    this.$router.push({
      path: '/home/discover1',
      query: {
        interviewer: item.name
      }
    });
  } 
  // 物联网产品岗跳转到action路由
  else if (item.name === "物联网产品岗面试官") {
    this.$router.push({
      path: '/action',
      query: {
        interviewer: item.name
      }
    });
  }
  // 其他岗位跳转到默认对话页面
  else {
    this.$router.push(`/dialog/${item.name}`);
  }
  
  // 关闭下拉菜单
  this.messageDropdownOpen = false;
},
navigateTo(path, name) {
  this.$router.push(path)
  this.active = name
},
handleLeftClick() {
  this.$router.push('/side')
},
handleTopAvatarClick() {
  this.$router.push('/side')
},
handleSidebarAvatarClick() {
  this.$router.push('/side')
},
handleActionSelect(action) {
  if (action.text === 'juli') {
    window.location.href = 'https://123.56.203.202/lip_sync'
  }
  this.showPopover = false
},
    // 专门用于从localStorage加载用户名的方法
    loadUsernameFromLocalStorage() {
      try {
        const userInfo = localStorage.getItem('currentUser')
        if (userInfo) {
          const parsedUser = JSON.parse(userInfo)
          this.username = parsedUser.username || '用户'
        } else {
          this.username = '访客'
        }
      } catch (error) {
        console.error('从localStorage加载用户名失败:', error)
        this.username = '用户'
      }
    }
  },
  watch: {
    '$router.path'(newPath) {
      const pathMap = {
        '/home/message': 'message',
        '/home/friends': 'friends',
        '/home/discover': 'discover'
      }
      this.active = pathMap[newPath] || 'message'
    }
  },
  mounted() {
    const pathMap = {
      '/home/message': 'message',
      '/home/friends': 'friends',
      '/home/discover': 'discover'
    }
    this.active = pathMap[this.$route.path] || 'message'
    this.loadUsernameFromLocalStorage()
  }
}
</script>

