import { createApp } from 'vue'
// 补充引入 List、Popup 和 Loading 组件
import { Toast, Button, Form, Field, CellGroup, NavBar, Icon, Image as VanImage,
    Tabbar, TabbarItem, Popover, SwipeCell, Cell, Tag, Search, PullRefresh, Tab, Tabs,
    Collapse, CollapseItem, IndexBar, IndexAnchor, Row, Col, Popup, Loading, List,DropdownMenu, DropdownItem } from 'vant'  // 新增List
import App from './App.vue'
import router from './router'
// 引入Vant样式（确保添加此行）
import 'vant/lib/index.css';

const app = createApp(App);
// 注册已有的组件
app.use(DropdownMenu);
app.use(DropdownItem);
app.use(Toast);
app.use(Button);
app.use(Form);
app.use(Field);
app.use(CellGroup);
app.use(NavBar);
app.use(Icon);
app.use(VanImage);
app.use(Tabbar);
app.use(TabbarItem);
app.use(Popover);
app.use(SwipeCell);
app.use(Cell);
app.use(Tag);
app.use(Search);
app.use(PullRefresh);
app.use(Tab);
app.use(Tabs);
app.use(Collapse);
app.use(CollapseItem);
app.use(IndexBar);
app.use(IndexAnchor);
app.use(Row);
app.use(Col);
// 新增：注册 Popup、Loading 和 List 组件
app.use(Popup);
app.use(Loading);
app.use(List);  // 注册List组件

app.use(router);
app.mount('#app');
