<template>
    <div class="nav-container">
        
        
        <!-- 加载状态 -->
        <div v-if="isLoading" class="loading">
            <p>正在加载动态...</p>
        </div>
        
        <!-- 错误提示 -->
        <div v-if="errorMessage" class="error">
            <p>⚠️ {{ errorMessage }}</p>
        </div>
        
        <!-- 无数据提示 -->
        <div v-if="!isLoading && !errorMessage && sortedBlogs.length === 0" class="no-data">
            <p>暂无博客内容</p>
        </div>
        
        <!-- 博客列表 -->
        <div class="blog-container" v-if="!isLoading && !errorMessage && sortedBlogs.length > 0">
            <el-card v-for="blog in sortedBlogs" :key="blog.id || blog.date" @click="openBlogModal(blog)" class="blog-card">
                <template #header>
                    <h3>题目：{{ blog.title }}</h3>
                    <h4>作者：{{ blog.username }}</h4>
                    <span class="blog-date">发布时间：{{ formatDate(blog.date) }}</span>
                    <p class="comment-count">评论数量：{{ getCommentsForBlog(blog.date).length }}</p>
                </template>
                <!-- 显示博客内容摘要 -->
                <div class="blog-content">
                    {{ blog.content ? (blog.content.length > 150 ? blog.content.slice(0, 150) + '... 点击查看全文' : blog.content) : '无内容' }}
                </div>
            </el-card>
        </div>
        
        <!-- 博客详情弹窗 -->
        <div v-if="showBlogModal" class="modal-overlay" @click="closeBlogModal">
            <div class="modal-content" @click.stop>
                <div class="modal-header">
                    <h2>{{ currentBlog.title }}</h2>
                    <button class="close-btn" @click="closeBlogModal">×</button>
                </div>
                <div class="modal-body">
                    <div class="blog-meta">
                        <span class="author">作者：{{ currentBlog.username }}</span>
                        <span class="date">发布时间：{{ formatDate(currentBlog.date) }}</span>
                    </div>
                    <div class="full-content">
                        {{ currentBlog.content || '该博客暂无内容' }}
                    </div>
                </div>
                <div class="modal-footer">
                    <button class="close-modal-btn" @click="closeBlogModal">关闭</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';

// 核心数据
const sortedBlogs = ref([]);
const allComments = ref([]);
const isLoading = ref(true);      // 加载状态
const errorMessage = ref('');     // 错误信息
const showBlogModal = ref(false); // 弹窗显示状态
const currentBlog = ref({});      // 当前选中的博客

// 格式化日期
const formatDate = (dateString) => {
    try {
        return new Date(dateString).toLocaleString('zh-CN', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit',
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit'
        });
    } catch (error) {
        console.error('日期格式化失败:', error);
        return dateString; // 格式化失败时显示原始字符串
    }
};

// 获取指定博客的评论
const getCommentsForBlog = computed(() => (blogDate) => {
    if (!allComments.value) return [];
    return allComments.value.filter(comment => comment && comment.blogDate === blogDate);
});

// 获取博客数据
const fetchBlogs = async () => {
    try {
        isLoading.value = true;
        errorMessage.value = '';
        
        console.log('开始请求博客数据...');
        const [blogsRes, commentsRes] = await Promise.all([
            axios.get('https://123.56.203.202/getBlogs'),
            axios.get('https://123.56.203.202/getComments')
        ]);
        
        console.log('获取到博客数据:', blogsRes.data);
        console.log('获取到评论数据:', commentsRes.data);
        
        // 确保数据是数组格式
        sortedBlogs.value = Array.isArray(blogsRes.data) ? blogsRes.data : [];
        allComments.value = Array.isArray(commentsRes.data) ? commentsRes.data : [];
        
    } catch (error) {
        console.error('获取数据失败:', error);
        errorMessage.value = '加载博客失败，请稍后重试';
        if (error.response) {
            console.error('请求错误:', error.response.status, error.response.data);
        } else if (error.request) {
            console.error('无响应:', error.request);
        }
    } finally {
        isLoading.value = false;
    }
};

// 打开博客详情弹窗
const openBlogModal = (blog) => {
    currentBlog.value = { ...blog }; // 复制博客数据
    showBlogModal.value = true;
    document.body.style.overflow = 'hidden'; // 禁止背景滚动
};

// 关闭博客详情弹窗 - 移除未使用的参数
const closeBlogModal = () => {
    showBlogModal.value = false;
    document.body.style.overflow = ''; // 恢复背景滚动
    currentBlog.value = {};
};

// 页面加载时获取博客数据
onMounted(async () => {
    await fetchBlogs();
});
</script>

