<script>
export default {
    data() {
        return {
            username: '',
            // 诗词列表 - 包含岳飞、苏轼、李煜、李白的经典诗句
            poetryList: [
                {  content: "怒发冲冠，凭栏处" }, // 岳飞《满江红》
                { icon: 'road', content: "人生如逆旅，我亦是行人" }, // 苏轼《临江仙》
                { icon: 'moon', content: "世事一场大梦，人生几度秋凉" }, // 苏轼《西江月》
                { icon: 'boat', content: "小舟从此逝，江海寄余生" }, // 苏轼《临江仙》
                { icon: 'teardrop', content: "问君能有几多愁？恰似一江春水向东流" }, // 李煜《虞美人》
                { icon: 'laugh', content: "仰天大笑出门去，我辈岂是蓬蒿人" }, // 李白《南陵别儿童入京》
                { icon: 'sword', content: "莫等闲，白了少年头，空悲切" }, // 岳飞《满江红》
                { icon: 'wine', content: "人生得意须尽欢，莫使金樽空对月" }, // 李白《将进酒》
                { icon: 'cloud', content: "人生如逆旅，我亦是行人" }, // 苏轼《临江仙》
                { icon: 'leaf', content: "剪不断，理还乱，是离愁" }, // 李煜《相见欢》
                { icon: 'mountain', content: "会当凌绝顶，一览众山小" } // 杜甫《望岳》
            ]
        };
    },
    methods: {
        // 返回
        back() {
            history.back();
        },
        // 打开我的个性名片
        openMyCard() {
            this.$router.push('/card?profile=my_profile.jpg&name='+ (this.username || '朽木') +'&qq=1164585349&gender=男&age=21&birth_month=10&birth_day=22&constellation=天秤&place=广东广州&sign=此心光明，亦复何虑。');
        }
    },
    mounted() {
        // 从localStorage获取用户名
        const userInfo = localStorage.getItem('currentUser');
        if (userInfo) {
            try {
                const parsedUser = JSON.parse(userInfo);
                this.username = parsedUser.username || '';
            } catch (error) {
                console.error('解析用户信息失败', error);
                this.username = '';
            }
        }
    }
}
</script>

