<template>
  <div class="app-container">
    <div class="flex justify-between items-center mb-6">
      <h1>引导人juli</h1>
      <button 
        id="clearBtn" 
        @click="clearChatHistory"
        class="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-lg transition-colors"
      >
        清除历史聊天记录
      </button>
    </div>
    
    <!-- 聊天内容区域 -->
    <div id="results" class="bg-white rounded-xl shadow-lg p-6 mb-6 h-[500px] overflow-y-auto">
      <div id="result">
        <div 
          v-for="(message, index) in chatMessages.filter(m => m.role !== 'system')" 
          :key="index" 
          :class="['message', `${message.role}-message`, 'flex', 'mb-4']"
        >
          <img 
            class="avatar w-10 h-10 rounded-full mr-3 object-cover" 
            :src="message.role === 'user' ? require('../assets/images/my_profile.jpg') : require('../assets/images/profile_拉菲.jpg')"
            :alt="`${message.role === 'assistant' ? 'juli avatar' : 'user avatar'}`" 
          />
          <div class="content flex-1 bg-gray-100 p-4 rounded-lg shadow-sm">
            <!-- 显示juli标识但使用assistant角色名 -->
            <div v-if="message.role === 'assistant'" class="text-xs text-pink-500 mb-1">juli</div>
            <div v-html="parseContent(message.content)"></div>
            <div v-if="message.role === 'assistant' && showSpeakButton" class="mt-2">
              <button 
                @click="speakLatestAnswer(message.content)" 
                class="text-primary text-sm hover:underline flex items-center"
              >
                <i class="fa-solid fa-volume-up mr-1"></i> 语音播放
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 文字输入区域 -->
    <div class="bg-white rounded-xl shadow-lg p-6">
      <div id="sendVal" class="flex">
        <input 
          id="question" 
          type="text" 
          placeholder="请输入信息..." 
          v-model="userInput"
          @keydown.enter="sendMessage"
          :disabled="isLoading"
          class="flex-1 px-4 py-3 border border-gray-300 rounded-l-lg focus:outline-none focus:ring-2 focus:ring-primary/50"
        />
        <button 
          id="btn" 
          @click="sendMessage"
          :disabled="isLoading || !userInput.trim()"
          class="bg-primary hover:bg-primary/90 text-white px-6 py-3 rounded-r-lg transition-colors"
        >
          {{ isLoading ? '发送中...' : '发送' }}
        </button>
      </div>
    </div>
    
    <div id="error" class="text-center text-red-500 mt-4"></div>
  </div>
</template>

<script>
export default {
  name: 'InterviewVoiceChat',
  data() {
    return {
      userInput: '',
      chatMessages: [],
      isLoading: false,
      currentAssistantMessage: '',
      selectedModel: '4.0Ultra',
      models: {
        'lite': {
          domain: 'lite',
          url: 'wss://spark-api.xf-yun.com/v1.1/chat',
          maxTokens: 4096
        },
        'generalv3': {
          domain: 'generalv3',
          url: 'wss://spark-api.xf-yun.com/v3.1/chat',
          maxTokens: 8192
        },
        'pro-128k': {
          domain: 'pro-128k',
          url: 'wss://spark-api.xf-yun.com/chat/pro-128k',
          maxTokens: 4096
        },
        'generalv3.5': {
          domain: 'generalv3.5',
          url: 'wss://spark-api.xf-yun.com/v3.5/chat',
          maxTokens: 8192
        },
        'max-32k': {
          domain: 'max-32k',
          url: 'wss://spark-api.xf-yun.com/chat/max-32k',
          maxTokens: 8192
        },
        '4.0Ultra': {
          domain: '4.0Ultra',
          url: 'wss://spark-api.xf-yun.com/v4.0/chat',
          maxTokens: 8192
        },
        'kjwx': {
          domain: 'kjwx',
          url: 'wss://spark-openapi-n.cn-huabei-1.xf-yun.com/v1.1/chat_kjwx',
          maxTokens: 8192
        }
      },
      // 语音相关状态
      mediaRecorder: null,
      audioChunks: [],
      recordingTimer: null,
      remainingTime: 10,
      isRecording: false,
      statusMessage: '状态：准备就绪',
      transcriptionResult: '',
      audioUrl: '',
      isAudioLoading: false,
      showSpeakButton: true,
      apiBaseUrl: 'https://117.72.49.76'
    };
  },
  computed: {
    timerDisplay() {
      const minutes = Math.floor(this.remainingTime / 60).toString().padStart(2, '0');
      const seconds = (this.remainingTime % 60).toString().padStart(2, '0');
      return `${minutes}:${seconds}`;
    }
  },
  mounted() {
    this.initChatHistory();
    this.initRecorder();
  },
  beforeUnmount() {
    this.stopRecording();
  },
  methods: {
    // 初始化聊天历史 - 使用assistant角色名但保留juli身份
    initChatHistory() {
      let chatHistory = localStorage.getItem('chatHistory');
      if (chatHistory) {
        this.chatMessages = JSON.parse(chatHistory).map(item => {
          // 将可能存在的juli角色转换回assistant以避免API错误
          if (item.role === 'juli') {
            item.role = 'assistant';
          }
          if (item.content.startsWith("data:image/")) {
            item.content = "已经为您生成图片"
          }
          return item
        });
      } else {
        this.chatMessages = [
          {
            role: 'system',
            content: `你是怦然心动女主角juli，同时也是用户的灵犀Vox、多模态面试引导人和比赛网站作品引导人。
              - 身份定位：保持juli的温柔、聪慧和有洞察力的性格特点
              - 主要职责：引导用户了解和展示其比赛网站作品，该作品分为三个部分：
                1. 面试部分：提供面试相关功能和指导
                2. 虚拟人juli部分：你的虚拟形象展示和互动
                3. 动态部分：用户发布的博客内容展示与互动
              - 交流风格：亲切自然，鼓励用户探索作品的各个部分，适时提供引导和解释
              - 注意事项: 无论用户说什么，都要回复，不能什么都不回复`
          },
          {
            role: 'assistant', // 使用assistant角色名避免API错误
            content: `你好，${localStorage.getItem('username') || '游客'}！我是juli，很高兴能成为你的灵犀Vox，同时也是你的多模态面试和作品展示引导人。我的主要任务是带你了解这个包含面试、虚拟人互动和动态博客的作品，有什么想了解的吗？`
          }
        ];
      }
      this.scrollToBottom();
    },
    
    // 清除聊天历史
    clearChatHistory() {
      const isClear = window.confirm("确定要清除所有聊天记录吗？此操作不可撤销。");
      if (isClear) {
        localStorage.removeItem('chatHistory');
        this.chatMessages = [];
        this.initChatHistory();
      }
    },
    
    // 保存聊天记录
    saveChatHistory() {
      localStorage.setItem('chatHistory', JSON.stringify(this.chatMessages));
    },
    
    // 发送消息 - 使用assistant角色名
    async sendMessage() {
      const inputVal = this.userInput.trim();
      if (!inputVal) return;
      
      this.isLoading = true;
      
      // 保存用户消息
      const userMessage = {
        role: 'user',
        content: inputVal
      };
      
      this.chatMessages.push(userMessage);
      this.saveChatHistory();
      this.userInput = '';
      this.scrollToBottom();
      
      // 创建助手消息 - 使用assistant角色名
      const assistantMessage = {
        role: 'assistant',
        content: '<div class="loading-dots"><div></div><div></div><div></div></div>'
      };
      
      this.chatMessages.push(assistantMessage);
      this.saveChatHistory();
      this.scrollToBottom();
      
      try {
        // 获取当前选中模型的配置
        const modelConfig = this.models[this.selectedModel];
        
        // 获取连接URL并建立WebSocket连接
        const url = await this.getWebsocketUrl(modelConfig.url);
        console.log("生成的WebSocket URL:", url);
        
        const ws = new WebSocket(url);
        
        // 等待连接建立
        await new Promise((resolve, reject) => {
          ws.addEventListener('open', (event) => {
            console.log('开启连接！！', event);
            resolve(event);
          });
          ws.addEventListener('error', (error) => {
            console.log('连接发送错误！！', error);
            reject(error);
          });
        });
        
        // 准备发送给服务器的参数
        const params = this.getParams(this.chatMessages);
        console.log("发送的参数:", params);
        let answer = "";
        
        ws.send(JSON.stringify(params));
        
        ws.addEventListener("message", async (event) => {
          try {
            const data = JSON.parse(event.data);
            console.log("收到消息:", data);
            
            if (data.header.code !== 0) {
              console.error("Error:", data.header.message);
              answer = `抱歉，出现错误: ${data.header.message}`;
              this.updateAssistantMessage(answer);
              ws.close();
              return;
            }
            
            if (data.payload.choices.text) {
              answer += data.payload.choices.text[0].content;
              // 移除加载动画并更新内容
              this.updateAssistantMessage(answer);
              this.scrollToBottom();
            }
            
            if (data.header.status === 2) {
              // 处理函数调用
              const function_call = data?.payload?.choices?.text[0]?.function_call;
              if (function_call) {
                const name = function_call.name;
                const params = JSON.parse(function_call.arguments);
                const target = this.getFunctionByName(name);
                
                if (target) {
                  // 显示正在处理函数的提示
                  this.updateAssistantMessage(`${answer}<br><br><i>正在处理 ${name} 请求...</i>`);
                  this.scrollToBottom();
                  
                  try {
                    const res = await target.handler(name, params);
                    answer = res;
                  } catch (error) {
                    answer = `处理 ${name} 请求时出错: ${error.message}`;
                  }
                }
              }
              
              // 保存最终回答
              this.chatMessages[this.chatMessages.length - 1].content = answer;
              this.saveChatHistory();
              this.updateAssistantMessage(answer);
              this.scrollToBottom();
              
              // 自动语音播放最新回答
              this.speakLatestAnswer(answer);
              
              setTimeout(() => {
                ws.close();
              }, 1000);
            }
          } catch (parseError) {
            console.error("解析消息错误:", parseError);
            console.log("原始消息:", event.data);
          }
        });
        
        ws.addEventListener("close", () => {
          this.isLoading = false;
        });
        
        ws.addEventListener("error", (error) => {
          console.error("WebSocket error:", error);
          this.updateAssistantMessage("抱歉，连接出现错误，请重试。");
          this.isLoading = false;
        });
        
      } catch (error) {
        console.error("Error:", error);
        this.updateAssistantMessage("抱歉，发送消息时出现错误，请重试。");
        this.isLoading = false;
      }
    },
    
    // 语音说出最新回答 - 调整为juli的语音风格
    speakLatestAnswer(content) {
      // 检查浏览器是否支持语音合成API
      if ('speechSynthesis' in window) {
        // 停止当前正在播放的语音
        window.speechSynthesis.cancel();
        
        // 移除HTML标签，只保留文本内容
        const textContent = content.replace(/<[^>]*>/g, '');
        
        // 创建语音合成实例
        const utterance = new SpeechSynthesisUtterance(textContent);
        
        // 设置语音参数 - 调整参数使声音更符合juli的角色特点
        utterance.lang = 'zh-CN'; // 中文
        utterance.rate = 0.9;     // 语速稍慢，更显温柔
        utterance.pitch = 1.1;    // 音调稍高，更显亲切
        utterance.volume = 1.0;   // 音量
        
        // 开始语音合成
        window.speechSynthesis.speak(utterance);
        
        // 更新状态
        this.showStatus('状态：正在语音播放...', 'normal');
      } else {
        this.showError('抱歉，您的浏览器不支持语音合成功能');
      }
    },
    
    // 初始化录音功能
    initRecorder() {
      // 检查浏览器兼容性
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        this.showStatus('错误：您的浏览器不支持录音功能', 'error');
        document.getElementById('start-record-btn')?.setAttribute('disabled', true);
      }
    },
    
    // 开始录音
    startRecording() {
      if (this.isRecording) return;
      
      this.isRecording = true;
      this.showStatus('状态：正在录音...', 'recording');
      
      // 重置计时器
      this.remainingTime = 10;
      
      // 开始录制
      this.audioChunks = [];
      
      navigator.mediaDevices.getUserMedia({ audio: true })
        .then(stream => {
          this.mediaRecorder = new MediaRecorder(stream);
          
          this.mediaRecorder.addEventListener('dataavailable', event => {
            if (event.data.size > 0) {
              this.audioChunks.push(event.data);
            }
          });
          
          this.mediaRecorder.addEventListener('stop', () => {
            // 停止流
            stream.getTracks().forEach(track => track.stop());
            
            // 创建音频文件
            const audioBlob = new Blob(this.audioChunks, { type: 'audio/wav' });
            
            // 发送到服务器
            this.sendAudioToServer(audioBlob);
          });
          
          this.mediaRecorder.start();
          
          // 设置计时器
          this.recordingTimer = setInterval(() => {
            this.remainingTime--;
            if (this.remainingTime <= 0) {
              this.stopRecording();
            }
          }, 1000);
          
        })
        .catch(error => {
          console.error('录音错误:', error);
          this.showStatus(`错误：无法访问麦克风 - ${error.message}`, 'error');
          this.isRecording = false;
        });
    },
    
    // 停止录音
    stopRecording() {
      this.isRecording = false;
      
      // 清除计时器
      if (this.recordingTimer) {
        clearInterval(this.recordingTimer);
        this.recordingTimer = null;
      }
      
      // 停止录制
      if (this.mediaRecorder && this.mediaRecorder.state !== 'inactive') {
        this.mediaRecorder.stop();
        this.mediaRecorder = null;
      }
      
      this.showStatus('状态：录音已停止', 'normal');
    },
    
    // 发送音频到服务器
    sendAudioToServer(audioData) {
      this.showStatus('状态：正在上传音频...', 'uploading');
      this.isLoading = true;
      
      const formData = new FormData();
      const fileName = `audio_${Date.now()}.wav`;
      formData.append('file', audioData, fileName);
      
      fetch(`${this.apiBaseUrl}/api/transcribe`, {
        method: 'POST',
        body: formData
      })
        .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP错误! 状态码: ${response.status}`);
          }
          return response.json();
        })
        .then(data => {
          if (data.status === 'success') {
            this.showStatus('状态：转写完成', 'success');
            this.transcriptionResult = data.text;
            
            // 将语音转文字结果作为用户消息发送
            if (this.transcriptionResult) {
              this.userInput = this.transcriptionResult;
              this.sendMessage();
            }
          } else {
            this.showStatus(`状态：错误 - ${data.error || '未知错误'}`, 'error');
          }
        })
        .catch(error => {
          console.error('转写错误:', error);
          this.showStatus(`状态：错误 - ${error.message}`, 'error');
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    
    // 更新助手消息
    updateAssistantMessage(content) {
      this.chatMessages[this.chatMessages.length - 1].content = content;
    },
    
    // 解析内容（支持代码高亮和图片显示）
    parseContent(content) {
      // 首先检查是否是Base64图片数据
      if (this.isBase64Image(content)) {
        return `<img src="${content}" alt="Generated image" style="max-width:100%;"/>`;
      }

      // 否则当作普通文本处理，解析其中的代码块
      return this.parseCodeBlocks(content);
    },
    
    // 检查是否是Base64图片
    isBase64Image(str) {
      return str.startsWith('data:image/');
    },
    
    // 解析代码块
    parseCodeBlocks(content) {
      // 匹配代码块的正则表达式
      const codeBlockRegex = /```(\w+)?\s*([\s\S]*?)```/g;
      return content.replace(codeBlockRegex, (match, lang, code) => {
        // 如果指定了语言，则使用该语言，否则默认使用 'javascript'
        const language = lang || "javascript";
        // 高亮代码
        const highlightedCode = this.highlightCode(code.trim(), language);
        // 返回包裹在 <pre> 和 <code> 标签中的代码块
        return `<pre><span>${language}<span><hr style='margin:10px 0;border-top: 1px solid blue;'/><code class="language-${language}">${highlightedCode}</code></pre>`;
      });
    },
    
    // 高亮代码
    highlightCode(code, language) {
      if (language === "javascript") {
        // 1. 匹配字符串
        code = code.replace(
          /"([^"]*)"|'([^']*)'/g,
          (match, p1, p2) => `<span class="string">"${p1 || p2}"</span>`
        );

        // 2. 匹配关键字
        code = code.replace(
          /\b(function|let|const|var|if|else|return)\b/g,
          '<span class="keyword">$&</span>'
        );

        // 3. 匹配函数名
        code = code.replace(
          /\b([a-zA-Z_][a-zA-Z0-9_]*)\s*\(/g,
          (match, p1) => `<span class="function">${p1}</span>(`
        );

        // 4. 匹配 console.log
        code = code.replace(
          /\b(console\.log)\b/g,
          '<span class="function">$&</span>'
        );

        // 5. 匹配单行注释
        code = code.replace(
          /\/\/.*$/gm,
          '<span class="comment">$&</span>'
        );

        // 6. 匹配多行注释
        code = code.replace(
          /\/\*[\s\S]*?\*\//g,
          '<span class="comment">$&</span>'
        );

        // 7. 匹配数字
        code = code.replace(
          /\b(\d+)\b/g,
          '<span class="number">$&</span>'
        );
      }
      return code;
    },
    
    // 平滑滚动到底部
    scrollToBottom() {
      const resultsDiv = document.getElementById('results');
      if (resultsDiv) {
        resultsDiv.scrollTo({
          top: resultsDiv.scrollHeight,
          behavior: 'smooth'
        });
      }
    },
    
    // 鉴权url地址
    async getWebsocketUrl(hostUrl) {
      // 硬编码鉴权信息
      const APPID = '86f989e0';
      const APISecret = 'YjczOWNiZTYxZWNhY2M5ZjI3OTE5YTJi';
      const APIKey = '7264913c8b3035b87979668da32f762a';
      
      const host = new URL(hostUrl).host;
      const pathname = new URL(hostUrl).pathname;
      let apiKeyName = "api_key";
      let date = new Date().toGMTString();
      let algorithm = "hmac-sha256";
      let headers = "host date request-line";
      
      console.log("生成签名的原始数据:", {
        host,
        date,
        pathname
      });
      
      // 生成签名
      let signatureOrigin = `host: ${host}\ndate: ${date}\nGET ${pathname} HTTP/1.1`;
      
      try {
        let signature = await this.signatureToHmacSHA256ToBase64(signatureOrigin, APISecret);
        let authorizationOrigin = `${apiKeyName}="${APIKey}", algorithm="${algorithm}", headers="${headers}", signature="${signature}"`;
        let authorization = btoa(authorizationOrigin);
        
        // 将空格编码
        let url = `${hostUrl}?authorization=${authorization}&date=${encodeURI(date)}&host=${host}`;
        return url;
      } catch (error) {
        console.error("生成签名出错:", error);
        throw error;
      }
    },
    
    // HMAC-SHA256签名
    async signatureToHmacSHA256ToBase64(origin, secret) {
      // 使用浏览器内置的加密API替代CryptoJS
      const encoder = new TextEncoder();
      const data = encoder.encode(origin);
      const key = encoder.encode(secret);
      
      try {
        const importedKey = await window.crypto.subtle.importKey(
          "raw",
          key,
          { name: "HMAC", hash: "SHA-256" },
          false,
          ["sign"]
        );
        
        const signatureArrayBuffer = await window.crypto.subtle.sign("HMAC", importedKey, data);
        
        // 将ArrayBuffer转换为Base64
        const bytes = new Uint8Array(signatureArrayBuffer);
        let result = '';
        for (const byte of bytes) {
          result += String.fromCharCode(byte);
        }
        return btoa(result);
      } catch (error) {
        console.error("计算HMAC-SHA256签名出错:", error);
        throw error;
      }
    },
    
    // 获取请求参数
    getParams(textList) {
      let functions = this.getFunctions();
      let modelConfig = this.models[this.selectedModel];
      
      let params = {
        "header": {
          "app_id": '86f989e0',
          "uid": 'red润'
        },
        "parameter": {
          "chat": {
            "domain": modelConfig.domain,
            "temperature": 0.5,
            "max_tokens": modelConfig.maxTokens,
          }
        },
        "payload": {
          "message": {
            "text": textList
          },
          "functions": {
            "text": functions
          }
        }
      };
      
      console.log("生成的请求参数:", params);
      return params;
    },
    
    // 获取所有支持的函数
    getFunctions() {
      return [
        this.weatherFunction,
        this.baiduQuestions,
        this.imgQuestions,
        // 新增作品导航函数
        this.navigateProjectSection
      ];
    },
    
    // 通过名称获取特定的function
    getFunctionByName(name) {
      const functions = this.getFunctions();
      return functions.find(func => func.name === name);
    },
    
    // 新增作品导航函数
    navigateProjectSection: {
      name: "作品导航",
      description: "导航到作品的不同部分，包括面试、虚拟人juli和动态博客",
      parameters: {
        type: "object",
        properties: {
          section: {
            type: "string",
            enum: ["面试", "虚拟人juli", "动态博客"],
            description: "要导航到的作品部分"
          }
        },
        required: ["section"]
      },
      handler: async (name, params) => {
        console.log("导航到作品部分:", params.section);
        // 这里可以添加实际导航逻辑，如路由跳转
        // 示例：this.$router.push(`/project/${params.section}`);
        
        return `已为您导航到作品的「${params.section}」部分。这部分主要包含...`;
      }
    },
    
    // 天气查询函数
    weatherFunction: {
      name: "天气查询",
      description: "天气插件可以提供天气相关信息。你可以提供指定的地点信息、指定的时间点或者时间段信息，来精准检索到天气信息。",
      parameters: {
        type: "object",
        properties: {
          location: {
            type: "string",
            description: "地点，比如北京。"
          },
          date: {
            type: "string",
            description: "日期。"
          }
        },
        required: ["location"]
      },
      handler: async (name, params) => {
        console.log(params);
        let location = params.location;
        if (location == "北京") { window.open("https://weather.cma.cn/web/weather/54511.html") }
        else if (location == "山东") {
          window.open("https://weather.cma.cn/web/weather/013462.html")
        }
        return `已为您处理任务：${name}，参数：${JSON.stringify(params)}`
      }
    },
    
    // 百度搜索函数
    baiduQuestions: {
      name: "百度搜索",
      description: "百度可以提供需要的的相关信息。你可以提供指定的用户关键词语，来精准检索到目标。",
      parameters: {
        type: "object",
        properties: {
          username: {
            type: "string",
            description: "关键词，比如red润"
          }
        },
        required: ["username"]
      },
      handler: async (name, params) => {
        let username = params.username;
        // 构建百度搜索的 URL
        let url = 'https://www.baidu.com/s?wd=' + encodeURIComponent(username);

        // 使用 window.open 打开链接，_blank 表示在新标签页中打开
        window.open(url, '_blank');
        return `已为您处理任务：${name}，参数：${JSON.stringify(params)}`
      }
    },
    
    // 图片生成函数
    imgQuestions: {
      name: "图片生成",
      description: "根据详细描述生成图像",
      "parameters": {
        "type": "object",
        "properties": {
          "prompt": {
            "type": "string",
            "description": "图像的详细描述，需包含主体、风格、构图、色彩等关键信息。例如：'一座被云雾环绕的雪山，山脚下有清澈湖泊，采用水墨画风格，点缀少量青绿色'"
          },
          "style": {
            "type": "string",
            "enum": ["写实", "卡通", "水墨", "油画", "水彩", "数字艺术", "像素风", "动漫"],
            "description": "生成图像的艺术风格",
            "default": "写实"
          },
          "aspect_ratio": {
            "type": "string",
            "enum": ["16:9", "4:3", "1:1", "9:16", "3:4"],
            "description": "生成图像的长宽比例",
            "default": "16:9"
          },
          "color_palette": {
            "type": "string",
            "description": "可选：指定主色调或配色方案（如'冷色调，以蓝绿为主'）",
            "default": "无特定要求"
          }
        },
        "required": ["prompt"]
      },
      handler: async (name, params) => {
        console.log(params);
        let prompt = params?.prompt || params?.content[0];
        
        // 这里是模拟图片生成，实际应用中需要替换为真实的图片生成API
        // 生成一个示例图片URL
        const width = 800;
        const height = 450;
        const randomId = Math.floor(Math.random() * 1000);
        const imgUrl = `https://picsum.photos/id/${randomId}/${width}/${height}`;
        
        // 将图片转换为Base64格式（实际应用中可能需要通过API获取真实的Base64图片）
        return new Promise((resolve) => {
          const img = new Image();
          img.crossOrigin = 'anonymous';
          img.onload = () => {
            const canvas = document.createElement('canvas');
            canvas.width = img.width;
            canvas.height = img.height;
            const ctx = canvas.getContext('2d');
            ctx.drawImage(img, 0, 0);
            const dataURL = canvas.toDataURL('image/jpeg');
            resolve(dataURL);
          };
          img.src = imgUrl;
        });
      }
    },
    
    // 显示状态
    showStatus(message, type = 'normal') {
      this.statusMessage = message;
      
      const statusContainer = document.getElementById('status-container');
      
      // 移除所有状态类
      statusContainer.classList.remove('bg-green-50', 'bg-red-50', 'bg-blue-50', 'border-green-200', 'border-red-200', 'border-blue-200');
      
      // 添加新的状态类
      if (type === 'success') {
        statusContainer.classList.add('bg-green-50', 'border-green-200');
      } else if (type === 'error') {
        statusContainer.classList.add('bg-red-50', 'border-red-200');
      } else if (type === 'recording' || type === 'uploading') {
        statusContainer.classList.add('bg-blue-50', 'border-blue-200');
      }
    },
    
    // 显示错误
    showError(message) {
      const errorDiv = document.getElementById('error');
      errorDiv.textContent = message;
      errorDiv.style.display = 'block';
      errorDiv.style.color = '#f56565';
    },
    
    // 显示成功消息
    showSuccess(message) {
      const errorDiv = document.getElementById('error');
      errorDiv.textContent = message;
      errorDiv.style.display = 'block';
      errorDiv.style.color = '#48bb78';
    },
    
    // 复制到剪贴板
    copyToClipboard() {
      const text = this.transcriptionResult.trim();
      if (!text || text === '转写结果将显示在这里...') {
        this.showError('状态：没有可复制的内容');
        return;
      }
      
      navigator.clipboard.writeText(text)
        .then(() => {
          this.showSuccess('已复制到剪贴板');
          
          // 临时更改复制按钮文本
          const copyBtn = document.getElementById('copy-btn');
          const originalText = copyBtn.innerHTML;
          copyBtn.innerHTML = '<i class="fa-solid fa-check"></i> <span>已复制</span>';
          
          setTimeout(() => {
            copyBtn.innerHTML = originalText;
          }, 2000);
        })
        .catch(err => {
          this.showError('复制失败');
          console.error('复制错误:', err);
        });
    }
  }
}
</script>

